/**
 * Date_helpers
*/
class Date_helpers
{
	/**
	 * construct_days
	*/
	construct_days()
	{
		let now = new Date(/*2023, 0, 20*/);
		let last_day = new Date(now.getFullYear(), now.getMonth() + 1, 0).getDate();

		let days = [];
		var i;
		for (i = 1; i < last_day + 1; i++) {
			days.push(i);
		}

		now.setDate(1);
		let first_day = now.getDay();
		let add_empty = null;
		/*
		0 ne  6
		1 po  0
		2 ut  1
		3 str 2
		4 stv 3
		5 pi  4
		6 so  5
		*/
		if (first_day === 0) {
			add_empty = first_day + 6;
		} else {
			add_empty = first_day - 1;
		}

		for (i = 0; i < add_empty; i++) {
			days.unshift(0);
		}

		return days;
	}


	/**
	 * construct_week
	*/
	construct_week()
	{
		return [
			'Mon',
			'Tue',
			'Wed',
			'Thu',
			'Fri',
			'Sat',
			'Sun',
		];
	}


	/**
	 * construct_months
	*/
	construct_months()
	{
		return {
			January: 1,
			February: 2,
			March: 3,
			April: 4,
			May: 5,
			June: 6,
			July: 7,
			August: 8,
			September: 9,
			October: 10,
			November: 11,
			December: 12,
		};
	}


	/**
	 * construct_years
	*/
	construct_years()
	{
		let this_year = new Date().getFullYear();
		let decade = Math.ceil(this_year / 10) * 10;

		let years = [];
		let i;
		for (i = decade - 20; i < decade + 10; i++) {
			years.push(i);
		}

		return years;
	}
}

export default new Date_helpers;
