<template>
	<input
		:id="field_id"
		:name="field_id"
		v-model="value"
		type="text"
	>

	<div class="vdp-datepicker">
		<div class="calendar">
			<header>
				<span class="prev" @click="prev">&lt;</span>
				<span class="go_up" @click="go_up">
					May 2023
					<!-- 2023 -->
					<!-- 2010 - 2039 -->
				</span>
				<span class="next" @click="next">&gt;</span>
			</header>

			<!-- days -->
			<div
				v-if="calendar_type === 0"
			>
				<span
					v-for="weekday in construct_week"
					class="cell day-header"
				>
					{{ weekday }}
				</span>
				<span
					v-for="day in construct_days"
					@click="day_picked(day)"
					class="cell day"
					:class="[day === this_day ? 'today' : '', day === 0 ? 'blank' : '']"
				>
					{{ day === 0 ? '' : day }}
				</span>
			</div>

			<!-- months -->
			<div
				v-if="calendar_type === 1"
			>
				<span
					v-for="(number, month) in construct_months"
					@click="month_picked(number)"
					class="cell month"
					:class="number === this_month ? 'today' : ''"
				>
					{{ month }}
				</span>
			</div>

			<!-- years -->
			<div
				v-if="calendar_type === 2"
			>
				<span
					v-for="year in construct_years"
					@click="year_picked(year)"
					class="cell year"
					:class="year === this_year ? 'today' : ''"
				>
					{{ year }}
				</span>
			</div>

		</div>
	</div>

</template>












<script>
	import Date_helpers from '@/components/forms/date_library/Date_helpers.js';

	// displayed type: day, month, year
	// listener na sipky: zmena hore/dole podla typu
	// listener medzi sipkami: zmena na mesia alebo rok
	// listener na vyber: den, mesiac rok
	// vypis datumu do inputu
	// prijatie datumu zo session
	// prijatie datumu z inputu: user text input change
	// osetrenie chybnych formatov
	// zobrazenie podla dodaneho datumu, a dnesneho datumu
	// zobrazenie dnesneho datumu ako default ak doruceny prazdny string
	// zobrazenie vo vybranom type: den, mesiac, rok
	// zobrazenie kalendaru pre aktualny mesiac a spravne zoradenie dni
	// zobrazenie aktualneho roku
	// zobrazenie aktualnej dekady
	// disabled dates
	// reactivity events
	// store eventy
	// emity, product form, global form
	// otvori sa aj nad inputom

	export default {
		props: {
			field_id: String,
			field_data: Object,
			init_type: String, // days, months, years, eighties
		},
		components: {
		},
		emits: [],
		mixins: [],
		data() {
			return {
				calendar_type: undefined,
			}
		},
		created() {
			let type_definition = {
				days: 0,
				months: 1,
				years: 2,
				eighties: 3,
			};
			this.calendar_type = type_definition[this.init_type];
		},
		mounted() {
		},
		methods: {
			prev()
			{
				if (this.calendar_type === 0) {
					return;
				}
				this.calendar_type--;
			},
			next()
			{
				if (this.calendar_type === 0) {
					return;
				}
				this.calendar_type--;
			},
			go_up()
			{
				if (this.calendar_type === 2) {
					return;
				}
				this.calendar_type++;
			},
			day_picked(int)
			{
				console.log('day', int);
			},
			month_picked(int)
			{
				console.log('month', int);
			},
			year_picked(int)
			{
				console.log('year', int);
			},
		},
		computed: {
			construct_days()
			{
				return Date_helpers.construct_days();
			},
			construct_week()
			{
				return Date_helpers.construct_week();
			},
			construct_months()
			{
				return Date_helpers.construct_months();
			},
			construct_years()
			{
				return Date_helpers.construct_years();
			},
			this_day()
			{
				return new Date().getDate();
			},
			this_month()
			{
				return new Date().getMonth() + 1;
			},
			this_year()
			{
				return new Date().getFullYear();
			},
			value:
			{
				get() {
					return '';
				},
				set(event_value) {
					console.log(event_value);
				},
			},
		},
		watch: {
		},
	}
</script>





































